import "../styles/TransporterForm.css";

import { motion } from "framer-motion";

import React, { useState } from "react";

import exit from "../asstes/dashboard/exit.png";

import logo from "../asstes/logo.png";
import FileUploader from "./MUI/fileUploader";

import { getDatabase, ref, set, push, get, update } from "firebase/database";
import loader from "../asstes/loadingIcon.gif";

function ServiceForm({ setUpdateServiceView, close, updateService }) {
  const [name, setName] = useState("");
  const [price, setPrice] = useState("");
  const [selectedImage, setSelectedImage] = useState(null); // For image file
  const [resetUploader, setResetUploader] = useState(false); // For resetting the FileUploader

  const [loading, setLoading] = useState(false);

  const clear = () => {
    setName("");
    setPrice("");
    setSelectedImage(null);
    setResetUploader((prev) => !prev);
    setLoading(false);
  };

  const uploadImageToCloudinary = async (imageFile) => {
    const formData = new FormData();
    formData.append("file", imageFile);
    formData.append("upload_preset", "khadamat_upload"); // Replace with your Cloudinary upload preset
    formData.append("cloud_name", "dsnjvuoks"); // Replace with your Cloudinary cloud name

    try {
      const response = await fetch(
        "https://api.cloudinary.com/v1_1/dsnjvuoks/image/upload", // Replace with your Cloudinary API URL
        {
          method: "POST",
          body: formData,
        }
      );

      const data = await response.json();

      console.log("after response ");

      if (response.ok) {
        console.log("Image URL:", data.secure_url);
        return data.secure_url; // Return the URL of the uploaded image
      } else {
        console.log("Error uploading image:", data.error.message);
        return null;
      }
    } catch (error) {
      console.log("Error during upload:", error);
      return null;
    }
  };

  const submit = async () => {
    setLoading(true);
    console.log("uploading ....." + selectedImage);
    if (!selectedImage) {
      console.log("No image selected for upload");
      setLoading(false);
      return;
    }

    // Upload the image to Cloudinary
    const imageUrl = await uploadImageToCloudinary(selectedImage);

    if (imageUrl) {
      console.log("Image uploaded successfully:", imageUrl);

      // Uncomment and modify the following lines if you want to save the service details with the image URL later

      console.log("form valid");
      const db = getDatabase();
      push(ref(db, "services"), {
        name: name,
        price: price,
        imageUrl: imageUrl, // Add the image URL here
      }).then((snap) => {
        console.log(snap.key);
        const data = {
          id: snap.key,
        };

        const id = ref(db, "services/" + snap.key);

        update(id, data);
        setLoading(false);
      });

      setUpdateServiceView(true);
      updateService();
      clear();
      close();
      setLoading(false);

    
    }
  };

  return (
    <motion.div
      className="transporter-Form"
      initial={{ opacity: 0 }}
      animate={{ opacity: 1, transition: { delay: 2 } }}
    >
      <div
        className="exit-btn"
        onClick={() => {
          setLoading(false);
          clear();
          close();
        }}
      >
        <img src={exit} alt="" />
      </div>
      <p>New Service</p>

      <div className="flex-column align-center service-header-content">
        <FileUploader
          className={"margin30"}
          onFileSelect={(file) => setSelectedImage(file)}
          reset={resetUploader} // Pass reset state
        />

        <p className="small-text grey">
          If you want to set the service image, you can upload the service image
          from your device.
        </p>
      </div>

      <div className="form-grp">
        <div className="form-input">
          <p>Designation</p>
          <input
            type="text"
            placeholder="Service's Designation"
            value={name}
            onChange={(e) => {
              setName(e.target.value);
            }}
          />
        </div>

        <div className="form-input">
          <p>Price</p>
          <input
            type="text"
            placeholder="Service price"
            value={price}
            onChange={(e) => {
              setPrice(e.target.value);
            }}
          />
        </div>
      </div>

      <div className="form-actions margin30">
        <div
          className="form-action bkBlue relative"
          onClick={() => {
            if (!loading) submit();
          }}
        >
          <motion.div
            className="btn-loader"
            initial={{ opacity: 0 }}
            animate={loading ? { opacity: 1 } : { opacity: 0 }}
          >
            <img alt="" src={loader} />
          </motion.div>
          <motion.p
            initial={{ opacity: 1 }}
            animate={!loading ? { opacity: 1 } : { opacity: 0 }}
          >
            Submit
          </motion.p>
        </div>

        <div
          className="form-action second-action"
          onClick={() => {
            clear();
          }}
        >
          <p>Clear</p>
        </div>
      </div>
    </motion.div>
  );
}

export default ServiceForm;
