import React, { useState, useEffect } from "react";
import { motion } from "framer-motion";
import { getDatabase, ref, onValue } from "firebase/database";

const Notification = ({ page6 , setAnimateNotification , exitAnimateNotification}) => {
  const [notifications, setNotifications] = useState([]);
  const [workerStats, setWorkerStats] = useState({
    count: 0,
    lastRequest: null,
  });
  const [messageStats, setMessageStats] = useState({
    count: 0,
    lastMessage: null,
  });
  const [loading, setLoading] = useState(false);

  const fetchRealtimeData = () => {
    const db = getDatabase();
    setLoading(true);

    // Workers Notification Logic
    const workersRef = ref(db, "workers");
    onValue(workersRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        const requestWorkers = Object.values(data).filter(
          (worker) => worker.status === false
        );
        const sortedWorkers = requestWorkers.sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp));
        setWorkerStats({
          count: sortedWorkers.length,
          lastRequest: sortedWorkers[0] || null, // Most recent request by timestamp
        });

        setAnimateNotification();
        setTimeout(() => {
            exitAnimateNotification();
        }, 500);
      }
    });

    // Messages Notification Logic
    const messagesRef = ref(db, "messages");
    onValue(messagesRef, (snapshot) => {
      const data = snapshot.val();
      if (data) {
        const userMessages = Object.values(data)
          .filter((message) => message.sender !== "admin")
          .sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp));
        setMessageStats({
          count: userMessages.length,
          lastMessage: userMessages[0] || null, // Most recent message
        });
      }
    });

    setLoading(false);
  };

  useEffect(() => {
    fetchRealtimeData();
    console.log("updating....")
  }, []);

  // Format notifications for display
  const formatNotification = () => {
    const notificationsList = [];
    // Workers Notification
    if (workerStats.count > 0) {
      notificationsList.push(`Workers Request Account: ${workerStats.count}`);
      const lastRequest = workerStats.lastRequest;
      notificationsList.push(
        `Last worker request: ${lastRequest?.name || "Unknown"}`
      );
      notificationsList.push(`${lastRequest?.timestamp || "Unknown time"}`);
      notificationsList.push(

        <div className="action-btn " onClick={() => alert("See messages")}><p>See Workers</p></div>
      );
    }

    // Messages Notification
    if (messageStats.count > 0) {
      notificationsList.push(`Messages: ${messageStats.count} new messages`);
      const lastMessage = messageStats.lastMessage;
      notificationsList.push(
        `Last message: "${lastMessage?.text || "No content"}" (${
          lastMessage?.timestamp || "Unknown time"
        })`
      );
      notificationsList.push(

        <div className="action-btn " onClick={() => alert("See messages")}><p>See Messages</p></div>
      );
    }

    return notificationsList;
  };

  return (
    <motion.div
      className="notification-overlay"
      initial={{ scale: 0 }}
      animate={
        page6
          ? { scale: 1, origin: "left", transition: { ease: "easeInOut" } }
          : { scale: 0 }
      }
    >
      <div className="dash-page-title">
        <p>Khadamat</p>
        <p>Notifications</p>
      </div>
      {loading ? (
        <p>Loading...</p>
      ) : formatNotification().length > 0 ? (
        <ul>
          {formatNotification().map((notif, index) => (
            <li key={index}>{notif}</li>
          ))}
        </ul>
      ) : (
        <p>No new notifications</p>
      )}
    </motion.div>
  );
};

export default Notification;