import React, { useState, useEffect, useRef } from "react";
import { motion } from "framer-motion";
import { getDatabase, ref, get, push, onValue } from "firebase/database";
import "../styles/TransporterForm.css";
import actions from "../asstes/dashboard/actions.png";
import loadingIcon from "../asstes/dashboard/greenLoading.gif";

import { db } from "./database_config/firebaseConfig";

const WorkersList = ({
  setShowNewTransoprterOverlay,
  setSelectedTranspDetail,
  setShowTranspDetails,
  setShowDeleteWorkerConf,
  anime,
  setShowApproveConf,
  setShowRejectConf,
  setShowMessages
}) => {
  const [allTransporters, setAllTransporters] = useState([]);
  const [filteredTransporters, setFilteredTransporters] = useState([]);
  const [loading, setLoading] = useState(false);
  const [page, setPage] = useState(1);
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedStateFilter, setSelectedStateFilter] = useState(0); // 0: All, 1: Active, 2: Inactive
  const [selectedStatusFilter, setSelectedStatusFilter] = useState(0); // 0: All, 1: Activated, 2: Request
  const [showSelectOption1, setShowSelect1] = useState(false);
  const [showSelectOption2, setShowSelect2] = useState(false);
  const [selectedField, setSelectedField] = useState([
    true,
    true,
    true,
    true,
    true,
    true,
    true,
    true,
    true,
  ]);
  const [showOrderAction, setShowOrderAction] = useState(-1); // Track which list item is expanded
  const divRef = useRef(null);

  const getAllTransporter = (currentPage) => {
    const db = getDatabase();
    const workersRef = ref(db, "workers");

    setLoading(true);

    get(workersRef)
      .then((snapshot) => {
        const data = snapshot.val();
        if (!data) {
          console.log("No workers found");
          setAllTransporters((prev) => (currentPage === 1 ? [] : prev));
          setFilteredTransporters([]);
          setLoading(false);
          return;
        }

        const list = Object.entries(data)
          .map(([id, worker]) => ({
            id,
            name: worker.name,
            wilaya: worker.willaya,
            email: worker.email,
            Revenues: worker.Revenues,
            phone: worker.phone,
            state: worker.status,
            job: worker.job,
            experience: worker.experience,
            isValid: worker.isvalid,
            timestamp: worker.timestamp || "1900-01-01 00:00",
          }))
          .sort((a, b) => new Date(b.timestamp) - new Date(a.timestamp));

        const itemsPerPage = 50;
        const paginatedList = list.slice(0, currentPage * itemsPerPage);

        setTimeout(() => {
          setAllTransporters(paginatedList);
          applyFilters(
            paginatedList,
            selectedStateFilter,
            selectedStatusFilter
          );
          setLoading(false);
        }, 2000);
      })
      .catch((error) => {
        console.error("Error fetching workers:", error);
        setLoading(false);
      });
  };

  const handleScroll = () => {
    const element = divRef.current;
    if (
      element.scrollHeight - element.scrollTop - element.clientHeight < 5 &&
      !loading
    ) {
      setPage((prevPage) => prevPage + 1);
    }
  };

  const applyFilters = (workers, stateFilter, statusFilter , query) => {
    let filteredList = [...workers];

    // Apply Active/Inactive filter
    if (stateFilter === 1) {
      filteredList = filteredList.filter((worker) => worker.state);
    } else if (stateFilter === 2) {
      filteredList = filteredList.filter((worker) => !worker.state);
    }

    // Apply Status filter (Activated/Request)
    if (statusFilter === 1) {
      filteredList = filteredList.filter((worker) => worker.isValid);
    } else if (statusFilter === 2) {
      filteredList = filteredList.filter((worker) => !worker.isValid);
    }

    // Apply Search Filter
    if (query) {
      console.log("searchQuery: "+query);
      console.log(workers[0].name);

      filteredList = filteredList.filter((worker) =>

        worker.name  && worker.name.toLowerCase().includes(query.toLowerCase())
      
      );
    }
    setFilteredTransporters(filteredList);
  };

  const handleStateFilter = (filter) => {
    setSelectedStateFilter(filter);
    applyFilters(allTransporters, filter, selectedStatusFilter);
  };

  const handleStatusFilter = (filter) => {
    setSelectedStatusFilter(filter);
    applyFilters(allTransporters, selectedStateFilter, filter);
  };

  const handleSearchChange = (e) => {

    setSearchQuery(e.target.value);
    console.log(e.target.value);
    if(e.target.value === ""){
      setFilteredTransporters(allTransporters);
    }
      applyFilters(allTransporters, selectedStateFilter, selectedStatusFilter , e.target.value);
  };

  const toggleFieldVisibility = (index) => {
    const updatedFields = [...selectedField];
    updatedFields[index] = !updatedFields[index];
    setSelectedField(updatedFields);
  };

  const sortBy = (prop) => {
    const sorted = [...filteredTransporters].sort((a, b) => {
      if (a.name && prop === "name" ) {
        return a.name.localeCompare(b.name);
      }
      if (prop === "Revenues") {
        return b.Revenues - a.Revenues;
      }
      if (prop === "timestamp") {
        return new Date(b.timestamp) - new Date(a.timestamp);
      }
      return 0;
    });
    setFilteredTransporters(sorted);
  };

  useEffect(() => {
    getAllTransporter(page);
  }, [page]);

  useEffect(() => {
    applyFilters(allTransporters, selectedStateFilter, selectedStatusFilter);
  }, [allTransporters]);

  useEffect(() => {
    const dataRef = ref(db, "workers"); // Specify the path to your data in the database

    // Set up a listener for real-time updates
    const unsubscribe = onValue(dataRef, (snapshot) => {
      getAllTransporter(page + 1);
    });

    // Cleanup listener on component unmount
    return () => unsubscribe();
  }, []);

  return (
    <motion.div
      className="dashboard-orders tranporters-page"
      id="page3"
      initial={{ opacity: 0 }}
      animate={anime ? { opacity: 1, zIndex: 3 } : { opacity: 0, zIndex: 0 }}
    >
      <div className="tranporters-page-content">
        <div className="dash-page-title">
          <p>Khadamat</p>
          <p>Workers</p>
        </div>

        <div className="search-bar-container">
          <input
            type="text"
            placeholder="Search workers by name..."
            value={searchQuery}
            onChange={handleSearchChange}
            className="search-bar"
          />
        </div>

        <div className="orders-filters">
          <div onClick={() => handleStateFilter(0)}>
            <p>All</p>
            <motion.div
              className="selected-order-filter"
              animate={
                selectedStateFilter === 0 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
          </div>
          <div onClick={() => handleStateFilter(1)}>
            <p>Active</p>
            <motion.div
              className="selected-order-filter"
              animate={
                selectedStateFilter === 1 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
          </div>
          <div onClick={() => handleStateFilter(2)}>
            <p>Inactive</p>
            <motion.div
              className="selected-order-filter"
              animate={
                selectedStateFilter === 2 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
          </div>

          <div onClick={() => handleStatusFilter(0)}>
            <p>All Status</p>
            <motion.div
              className="selected-order-filter"
              animate={
                selectedStatusFilter === 0 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
          </div>
          <div onClick={() => handleStatusFilter(1)}>
            <p className=""> Activated</p>
            <motion.div
              className="selected-order-filter"
              animate={
                selectedStatusFilter === 1 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
          </div>
          <div onClick={() => handleStatusFilter(2)}>
            <p>Request</p>
            <motion.div
              className="selected-order-filter"
              animate={
                selectedStatusFilter === 2 ? { width: "100%" } : { width: 0 }
              }
            ></motion.div>
          </div>
        </div>

        <div className="filters-container">
          <div
            className="filer-components"
            onClick={() => setShowSelect1(!showSelectOption1)}
          >
            <p>Customize</p>
            {showSelectOption1 && (
              <motion.div className="filter-select">
                {[
                  "#",
                  "Name",
                  "Job",
                  "Experience",
                  "Wilaya",
                  "Phone",
                  "Revenues",
                  "Status",
                  "On/Off",
                ].map((field, index) => (
                  <div
                    key={field}
                    className="filter-options"
                    onClick={() => toggleFieldVisibility(index)}
                  >
                    <p>{field}</p>
                    <motion.p className="select-text-action">
                      {selectedField[index] ? "hide" : "show"}
                    </motion.p>
                  </div>
                ))}
              </motion.div>
            )}
          </div>

          <div
            className="filer-components"
            onClick={() => setShowSelect2(!showSelectOption2)}
          >
            <p>Sort By</p>
            {showSelectOption2 && (
              <motion.div className="filter-select">
                <div className="filter-options" onClick={() => sortBy("name")}>
                  <p>Client Name</p>
                </div>
                <div
                  className="filter-options"
                  onClick={() => sortBy("Revenues")}
                >
                  <p>Revenues</p>
                </div>
                <div
                  className="filter-options"
                  onClick={() => sortBy("timestamp")}
                >
                  <p>Inscription Date</p>
                </div>
              </motion.div>
            )}
          </div>

          <div
            className="add-tranporter filer-components"
            onClick={() => setShowNewTransoprterOverlay(true)}
          >
            <p>New Worker</p>
          </div>
        </div>

        <div className="transporter-list" ref={divRef} onScroll={handleScroll}>
          <div className="order-list-item">
            {[
              "#",
              "Name",
              "Job",
              "Experience",
              "Wilaya",
              "Phone",
              "Revenues",
              "Status",
              "On/Off",
            ].map((field, index) => (
              <motion.p
                key={field}
                initial={{ opacity: 1 }}
                animate={
                  selectedField[index]
                    ? { opacity: 1 }
                    : { opacity: 0, display: "none" }
                }
              >
                {field}
              </motion.p>
            ))}
          </div>
          {filteredTransporters.map((item, index) => (
            <div key={item.id}>
              <div className="order-list-item">
                {[
                  index + 1,
                  item.name,
                  item.job,
                  item.experience,
                  item.wilaya,
                  item.phone,
                  item.Revenues,
                  item.isValid ? "Activated" : "Request",
                ].map((value, i) => (
                  <motion.p
                    key={i}
                    initial={{ opacity: 1 }}
                    animate={
                      selectedField[i]
                        ? { opacity: 1 }
                        : { opacity: 0, display: "none" }
                    }
                    className={
                      i === 7 && value === "Request"
                        ? "redText"
                        : i === 7 && value === "Activated"
                        ? "greenText"
                        : ""
                    }
                  >
                    {value}
                  </motion.p>
                ))}
                <motion.p>
                  <div
                    className={
                      !item.state
                        ? "stat-circle bkYellow white"
                        : "stat-circle bkBlue white"
                    }
                  >
                    {item.state ? <p>On</p> : <p>Off</p>}
                  </div>
                </motion.p>
                <div
                  className="action"
                  onClick={() =>
                    setShowOrderAction(showOrderAction === index ? -1 : index)
                  }
                >
                  <img src={actions} alt="More Actions" />
                </div>
              </div>

              <motion.div
                className="order-list-item order-actions-container"
                initial={{ height: 0, opacity: 0 }}
                animate={
                  showOrderAction === index
                    ? { height: "60px", opacity: 1 }
                    : { height: 0, opacity: 0 }
                }
              >
                <motion.div
                  className="order-action bkBlue"
                  onClick={() => {
          
                    setSelectedTranspDetail(item);
                    setShowMessages(true);
                  }}
                >
                  <p>Send message</p>
                </motion.div>

                <motion.div
                  className="order-action bkRed"
                  onClick={() => {
                    setSelectedTranspDetail(item);
                    setShowTranspDetails(true);
                  }}
                >
                  <p>More Details</p>
                </motion.div>
                <motion.div
                  className="order-action bkYellow"
                  onClick={() => {
                    setSelectedTranspDetail(item);
                    setShowDeleteWorkerConf(true);
                  }}
                >
                  <p>Remove</p>
                </motion.div>

                <motion.div
                  className="order-action bkBlue"
                  onClick={() => {
                    setSelectedTranspDetail(item);
                    setShowApproveConf(true);
                  }}
                >
                  <p>Approve</p>
                </motion.div>

                <motion.div
                  className="order-action bkred"
                  onClick={() => {
                    setSelectedTranspDetail(item);
                    setShowRejectConf(true);
                  }}
                >
                  <p>Reject</p>
                </motion.div>
              </motion.div>
            </div>
          ))}
          {loading && (
            <div className="loading-item">
              <img alt="Loading..." src={loadingIcon} className="logo-img" />
            </div>
          )}
        </div>
      </div>
    </motion.div>
  );
};

export default WorkersList;
