import "../styles/TransporterForm.css";

import { motion } from "framer-motion";

import React, { useState, useEffect } from "react";

import exit from "../asstes/dashboard/exit.png";
import actions from "../asstes/dashboard/actions.png";
import arrow from "../asstes/dashboard/arrow.png";

import { getDatabase, ref, set, push, get, remove } from "firebase/database";
import FileStorage from "./MUI/FileStorage";

function ServicePage({
  setShowDeleteServiceOverlay,
  updateView,
  setUpdateView,
  setSelectedTranspDetail,
  setShowTranspDetails,
  setShowNewTransoprterOverlay,
  setChoiceTransoporter,
  setShowTransporterInfos,
  anime,
  selectOrderTransoprter,
  setSelectTranspOverlay,
  showTransporterInfos,
  setShowOrdersOverlay,
}) {
  const [selectedStateFilter, setSelectedStateFilter] = useState(0);
  const [showSelectOption1, setShowSelect1] = useState(false);

  const initSelected = [true, true, true, true, true, true, true, true];
  const [selectedField, setSelectedField] = useState(initSelected);

  const [presAllOrders, setPresAllOrders] = useState([]);

  const [allOrders, setAllOrders] = useState([]);
  const [filterOrders, setFilterOrders] = useState([]);

  const [allTransporters, setAllTransporters] = useState([]);
  const [filteTransporters, setFilterTransporters] = useState([]);

  const [selectedDateAnim, setSelectedDateAnim] = useState(6);
  const [showOrderAction, setShowOrderAction] = useState(-1);
  const [selectedOrder, setSelectedOrder] = useState(null);
  const [selectedOrderToUpdate, setSelectedOrderToUpdate] = useState(null);
  const [showSelectOption2, setShowSelect2] = useState(false);

  const [nameRech, setNameRech] = useState("");

  function compare(a, b) {
    if (a.Revenues < b.Revenues) {
      console.log("cskc,sk,csk,scc");
      return 1;
    }
    if (a.Revenues > b.Revenues) {
      return -1;
    }
    return 0;
  }

  function compareName(a, b) {
    console.log("compareName");
    if (a.name.toUpperCase() < b.name.toUpperCase()) {
      console.log("cc");
      return -1;
    }
    if (a.name.toUpperCase() > b.name.toUpperCase()) {
      console.log("cc");
      return 1;
    }
    return 0;
  }

  const getPrevDate = (i) => {
    var date = new Date();
    date.setDate(date.getDate() - i);
    var res = {
      day: date.toString().split(" ")[0],
      date: date.toString().split(" ")[2],
    };
    return res;
  };

  const formatDate = (date) => {
    var dd = String(date.getDate()).padStart(2, "0");
    var mm = String(date.getMonth() + 1).padStart(2, "0"); //January is 0!
    var yyyy = date.getFullYear();

    var res = dd + "/" + mm + "/" + yyyy;
    return res;
  };

  const filterTransoprterOnSelect = (payment, from) => {
    console.log(payment + " " + from);

    var res = [];
    allTransporters.map((item) => {
      if (item.payment_method == payment && item.city == from) {
        res.push(item);
      }
    });

    setChoiceTransoporter(res);
  };

  const getOrdersByDate = (date) => {
    //must change : store all orders in a var

    console.log("getOrdersByDate.....");
    console.log(date);
    var res = [];
    presAllOrders.map((item) => {
      console.log(item);
      if (item.date === date) {
        res.push(item);
      }
    });

    setAllOrders(res);
    setFilterOrders(res);
  };

  const sortBy = (list, prop) => {
    var res = [];
    if (list === "orders") {
      console.log("sort by");
      if (prop === "name") {
        res = filterOrders;
        res.sort(compareName);
        console.log("name");
        setFilterOrders(res);
      } else {
        res = filterOrders;
        res.sort(compare);
        console.log("price");
        setFilterOrders(res);
      }
    } else {
      if (list === "clients") {
      } else {
        if (filteTransporters.length == 0) {
          console.log("sort transps");
          res = allTransporters;
          res.sort(compareName);
          setFilterTransporters(res);
        } else {
          res = filteTransporters;
          res.sort(compareName);
          setFilterTransporters(res);
        }
      }
    }
  };

  const filterByState = (filter) => {
    var filterRes = [];
    if (filter == "a") {
      setFilterOrders(allOrders);
    } else {
      allOrders.map((item) => {
        if (item.state === filter) {
          filterRes.push(item);
        }
      });

      setFilterOrders(filterRes);
    }
  };

  const getAllOrders = () => {
    console.log("getting all orders...");
    const db = getDatabase();
    const shipments = ref(db, "services");

    get(shipments).then((snapshot) => {
      const data = snapshot.val();
      const list = Object.entries(data);
      var clientorders = [];
      var index = 1;

      list.forEach(async (element) => {
        var client = await getClientById(element[1].client_id);
        console.log(client[1].name);

        clientorders.push({
          id: element[0],
          client: client[1].name,
          client_id: element[1].client_id,
          date: element[1].time_requested,
          from: element[1].charging_location,
          to: element[1].discharging_location,
          paiment: element[1].payment_method,
          price: element[1].price,
          state: element[1].state,
          weight: element[1].weight,
          vehicle_type: element[1].vehicle_type,
          description: element[1].description,
          size: element[1].size,
        });
        index++;
      });

      setAllOrders(clientorders);
      setPresAllOrders(clientorders);
    });
  };

  const rechServiceByName = () => {
    console.log("rech" + nameRech);
    var rechList = [];

    allTransporters.map((item) => {
      if (item.name.toUpperCase().includes(nameRech.toUpperCase())) {
        rechList.push(item);
      }
    });

    setFilterTransporters(rechList);
  };
  const getClientById = async (id) => {
    const db = getDatabase();
    const shipments = ref(db, "clients");
    let ss = await get(shipments);

    const data = ss.val();
    const list = Object.entries(data);
    for (let i = 0; i < list.length; i++) {
      let element = list[i];
      if (element[0] == id) {
        console.log(element);
        return element;
      }
    }

    return "cc";
  };

  const getAllTransporter = () => {
    const db = getDatabase();
    const shipments = ref(db, "services");

    get(shipments).then((snapshot) => {
      const data = snapshot.val();
      const list = Object.entries(data);
      console.log(list);
      var transps = [];
      list.forEach((element) => {
        console.log("agent exsist");
        let obj = {
          id: element[0],
          name: element[1].name,
          price: element[1].price,
          imageUrl: element[1].imageUrl,
        };
        obj.set = (nData) => {
          let nt = { ...obj };
          Object.keys(nData).forEach((key, ind) => {
            nt[key] = nData[key];
          });

          let na = [...transps];
          let i = transps.indexOf(obj);
          na.splice(i, 1, nt);

          setSelectedTranspDetail(nt);
          setAllTransporters(na);
          setChoiceTransoporter(na);
        };
        transps.push(obj);

        console.log(element[1].name);
      });

      setAllTransporters(transps);

      rechServiceByName();
    });
  };

  const filterByStatus = (filter) => {
    var filterRes = [];
    if (filter === "a") {
      setFilterTransporters(allTransporters);
    } else {
      allTransporters.map((item) => {
        console.log(item.state);
        if (item.state == filter) {
          filterRes.push(item);
        }
      });

      setFilterTransporters(filterRes);
    }
  };

  useEffect(() => {
    getAllTransporter();

    setUpdateView(false);
  }, [updateView]);

  useEffect(() => {
    rechServiceByName();
  }, [nameRech]);

  useEffect(() => {
    rechServiceByName();
  }, [allTransporters]);

  useEffect(() => {
    getAllOrders();
    getAllTransporter();

    window.addEventListener("click", function (e) {
      if (
        document.getElementById("select-option") != null &&
        !document.getElementById("select-option").contains(e.target)
      ) {
        setShowSelect1(false);
      }

      if (
        document.getElementById("select-option2") != null &&
        !document.getElementById("select-option2").contains(e.target)
      ) {
        setShowSelect2(false);
      }
    });
  }, []);

  return (
    <motion.div
      className="dashboard-orders tranporters-page"
      id="page3"
      initial={{ opacity: 0 }}
      animate={anime ? { opacity: 1, zIndex: 3 } : { opacity: 0, zIndex: 0 }}
    >
      <div className="tranporters-page-content">
        <div className="dash-page-title">
          <p>Khadamat</p>
          <p>My Services</p>
        </div>

        <div className="filters-container">
          <div
            className="filer-components"
            onClick={() => {
              setShowSelect2(true);
            }}
          >
            <p>Sort By</p>

            <motion.div
              id="select-option2"
              className="filter-select"
              initial={{ opacity: 0, top: "50%" }}
              animate={
                showSelectOption2
                  ? {
                      opacity: 1,
                      top: "120%",
                      transition: { ease: "backInOut" },
                    }
                  : {
                      opacity: 0,
                      top: "50%",
                      transition: { ease: "backInOut" },
                    }
              }
            >
              <div
                className="filter-options"
                onClick={(e) => {
                  e.stopPropagation();
                  sortBy("", "");
                }}
              >
                <p>Revenues</p>
              </div>

              <div
                className="filter-options"
                onClick={() => {
                  sortBy("transp", "name");
                  console.log("sort");
                }}
              >
                <p>Client Name</p>
              </div>
            </motion.div>
          </div>

          <div
            className="add-tranporter filer-components"
            onClick={() => {
              setShowNewTransoprterOverlay(true);
            }}
          >
            <p>New Service</p>
          </div>
        </div>

        <div className="services-rech">
          <div className="form-input">
            <input
              type="text"
              placeholder="type Here Service's Name To Search"
              value={nameRech}
              onChange={(e) => {
                setNameRech(e.target.value);
              }}
            />
          </div>
        </div>

        <div className="services-list">
          {filteTransporters.length === 0
            ? allTransporters.map((item, index) => {
                return (
                  <div className="service-list-item-container">
                    <div className="order-list-item">
                      {item.imageUrl ? (
                        <img
                          alt="cc"
                          src={item.imageUrl}
                          className="service-image"
                        />
                      ) : (
                        <div className="service-image-placeholder">
                          <p className="small-text">No image Provided</p>
                        </div>
                      )}

                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[1]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.name}
                      </motion.p>
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[2]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.price} DA
                      </motion.p>

                      <div
                        className="action"
                        onClick={() => {
                          setShowOrderAction(index);
                        }}
                      >
                        <img src={actions} alt="" />
                      </div>
                    </div>

                    <motion.div
                      className="order-list-item order-actions-container"
                      initial={{ opacity: 0 }}
                      animate={
                        showOrderAction == index
                          ? {
                              top: "0",
                              height: "100%",
                              opacity: 1,
                              transition: { delay: 0 },
                            }
                          : {
                              top: "100%",
                              opacity: 0,
                              transition: { delay: 0 },
                            }
                      }
                    >
                      <motion.div
                        className="order-action bkRed"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction === index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setSelectedTranspDetail(item);
                          setShowTranspDetails(true);
                          setShowOrderAction(-1);
                        }}
                      >
                        <p>More details</p>
                      </motion.div>
                      <motion.div
                        className="order-action bkYellow"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setSelectedTranspDetail(item);
                          setShowDeleteServiceOverlay(true);
                          setShowOrderAction(-1);
                        }}
                      >
                        <p>Remove</p>
                      </motion.div>
                    </motion.div>
                  </div>
                );
              })
            : filteTransporters.map((item, index) => {
                return (
                  <div className="service-list-item-container">
                    <div className="order-list-item">
                      {item.imageUrl ? (
                        <img
                          alt="cc"
                          src={item.imageUrl}
                          className="service-image"
                        />
                      ) : (
                        <div className="service-image-placeholder">
                          <p className="small-text">No image Provided</p>
                        </div>
                      )}

                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[1]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.name}
                      </motion.p>
                      <motion.p
                        initial={{ opacity: 1 }}
                        animate={
                          selectedField[2]
                            ? { opacity: 1 }
                            : { display: "none", opacity: 0 }
                        }
                      >
                        {item.price} DA
                      </motion.p>

                      <div
                        className="action"
                        onClick={() => {
                          setShowOrderAction(index);
                        }}
                      >
                        <img src={actions} alt="" />
                      </div>
                    </div>

                    <motion.div
                      className="order-list-item order-actions-container"
                      initial={{ opacity: 0 }}
                      animate={
                        showOrderAction == index
                          ? {
                              top: "0",
                              height: "100%",
                              opacity: 1,
                              transition: { delay: 0 },
                            }
                          : {
                              top: "100%",
                              opacity: 0,
                              transition: { delay: 0 },
                            }
                      }
                    >
                      <motion.div
                        className="order-action bkRed"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setSelectedTranspDetail(item);
                          setShowTranspDetails(true);
                          setShowOrderAction(-1);
                        }}
                      >
                        <p>More details</p>
                      </motion.div>
                      <motion.div
                        className="order-action bkYellow"
                        initial={{ opacity: 0 }}
                        animate={
                          showOrderAction == index
                            ? { opacity: 1 }
                            : { opacity: 0 }
                        }
                        onClick={() => {
                          setSelectedTranspDetail(item);
                          setShowDeleteServiceOverlay(true);
                          setShowOrderAction(-1);
                        }}
                      >
                        <p>Remove</p>
                      </motion.div>
                    </motion.div>
                  </div>
                );
              })}
        </div>
      </div>
    </motion.div>
  );
}

export default ServicePage;
