import React, { useState, useEffect, useRef } from "react"; // Added useRef
import { motion } from "framer-motion";
import { getDatabase, ref, onValue, push } from "firebase/database";
import exit from "../asstes/dashboard/exit.png";
import loadingIcon from "../asstes/loadingIcon.gif";
import "../styles/TransporterForm.css";

function MessageOverlay({ open, close, worker }) {
  const [messages, setMessages] = useState([]);
  const [newMessage, setNewMessage] = useState("");
  const [loading, setLoading] = useState(false);

  const messagesEndRef = useRef(null); // Ref to track the bottom of the messages container
  const db = getDatabase();

  // Fetch messages from Firebase
  useEffect(() => {
    if (worker && open) {
      const messagesRef = ref(db, `messages/${worker.id}`);
      onValue(messagesRef, (snapshot) => {
        const data = snapshot.val();
        if (data) {
          setMessages(Object.values(data));
        } else {
          setMessages([]);
        }
        // Scroll to the bottom whenever messages are updated
        scrollToBottom();
      });
    }
  }, [worker, open]);

  useEffect(() => {
    setTimeout(() => {
      scrollToBottom();
    }, 500);
  });

  // Scroll to the bottom of the messages container
  const scrollToBottom = () => {
    if (messagesEndRef.current) {
      messagesEndRef.current.scrollIntoView({ behavior: "smooth" });
    }
  };

  // Save new message to Firebase
  const sendMessage = async () => {
    if (newMessage.trim() === "") return;

    setLoading(true);

    const messagesRef = ref(db, `messages/${worker.id}`);
    const messageData = {
      sender: "Admin",
      text: newMessage,
      timestamp: Date.now(),
    };

    try {
      await push(messagesRef, messageData);
      setNewMessage("");
    } catch (error) {
      console.error("Error sending message:", error);
    } finally {
      setLoading(false);
    }

    setNewMessage("");
  };

  // Handle key press events
  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      sendMessage();
    }
  };

  return (
    <motion.div
      className="transporter-Form message-overlay"
      initial={{ opacity: 0 }}
      animate={open ? { opacity: 1 } : { opacity: 0 }}
    >
      <div
        className="exit-btn"
        onClick={() => {
          close();
        }}
      >
        <img src={exit} alt="Close" />
      </div>

      <div className="centre-text">
        <p>Messages with {worker?.name}</p>
        <p className="my-20 grey">View and send messages to the worker.</p>
      </div>

      <div className="messages-container">
        {messages.map((message, index) => (
          <div
            key={index}
            className={`message ${
              message.sender === "Admin" ? "admin" : "worker"
            }`}
          >
            <p className="sender">{message.sender}</p>
            <p className="text">{message.text}</p>
            <p className="timestamp">
              {new Date(message.timestamp).toLocaleString()}
            </p>
          </div>
        ))}
        <div ref={messagesEndRef} />{" "}
        {/* Reference to ensure scroll to the bottom */}
      </div>

      <div className="form-grp msg-form-grp">
        <div className="form-input msg-input">
          <p>Message</p>
          <input
            type="text"
            placeholder="Type your message here..."
            value={newMessage}
            onChange={(e) => setNewMessage(e.target.value)}
            onKeyDown={handleKeyPress} // Added to handle Enter key press
          />
        </div>
      </div>

      <div className="form-actions">
        <div className="form-action bkBlue send-btn" onClick={sendMessage}>
          <p className="white btn">Send</p>
        </div>
      </div>
    </motion.div>
  );
}

export default MessageOverlay;
