import React, { useState, useEffect } from 'react';
import "../styles/dashboard.css";
import { motion } from "framer-motion";
import exit from "../asstes/dashboard/exit.png";
import loadingIcon from "../asstes/dashboard/greenLoading.gif";
import { getDatabase, ref, update } from "firebase/database";
import FileUploader from "./MUI/fileUploader";

const uploadImageToCloudinary = async (imageFile) => {
  const formData = new FormData();
  formData.append("file", imageFile);
  formData.append("upload_preset", "khadamat_upload"); // Replace with your Cloudinary upload preset
  formData.append("cloud_name", "dsnjvuoks"); // Replace with your Cloudinary cloud name

  try {
    const response = await fetch(
      "https://api.cloudinary.com/v1_1/dsnjvuoks/image/upload", // Replace with your Cloudinary API URL
      {
        method: "POST",
        body: formData,
      }
    );

    const data = await response.json();
    if (response.ok) {
      return data.secure_url; // Return the URL of the uploaded image
    } else {
      console.error("Error uploading image:", data.error.message);
      return null;
    }
  } catch (error) {
    console.error("Error during upload:", error);
    return null;
  }
};

const UpdateService = async (initialValues, transp, newImage) => {
  const db = getDatabase();
  let imageUrl = transp.imageUrl; // Keep the existing image by default

  if (newImage) {
    console.log("uploading image...")
    const uploadedImageUrl = await uploadImageToCloudinary(newImage);
    if (uploadedImageUrl) {
      imageUrl = uploadedImageUrl;
    }
  }

  const data = {
    name: initialValues.name,
    price: initialValues.price,
    imageUrl: imageUrl,
  };

  
  console.log("updating service...")


  const id = ref(db, 'services/' + transp.id);
  return update(id, data);
};

function TranspPapers({ transp, visible, close, setShowOrdersOverlay, setSelectedOrder, updateView }) {
  const [initialValues, setInitialValues] = useState({
    name: transp.name,
    price: transp.price,
  });

  const [newImage, setNewImage] = useState(null); // State to hold the new image file
  const [resetUploader, setResetUploader] = useState(false); // To reset the FileUploader
  const [loading, setLoading] = useState(false); // Loading state for image upload

  const handleRadioChange = (e, index) => {
    setInitialValues((prevValues) => ({
      ...prevValues,
      [index]: e.target.value,
    }));
  };

  const handleSave = async () => {
    setLoading(true);
    await UpdateService(initialValues, transp, newImage);
    setLoading(false);
    updateView();
    close();
  };

  return transp != null ? (
    <motion.div
      className="transporter-details-overlay"
      initial={{ scale: 0, opacity: 0, display: "none" }}
      animate={visible ? { scale: 1, display: "flex", opacity: 1 } : { scale: 0.5, opacity: 0 }}
      exit={{ display: "none" }}
    >
      {loading && (
        <motion.div className="preloader">
          <img src={loadingIcon} alt="Loading" />
          <p className='white'> Uploading Image and Updating Service...</p>
        </motion.div>
      )}
      <motion.div
        className="transporter-details-container service-update-container"
        id="transporter-details"
        initial={{ opacity: 0 }}
        animate={!loading ? { opacity: 1 } : { opacity: 0 }}
      >
        <div className="close-btn" onClick={close}>
          <img src={exit} alt="Close" />
        </div>
        <div className="transp-details-header">
          <p>Update Service</p>
          <p>{transp.name}</p>
        </div>

        <div className="form-input update-image">
            <FileUploader
              className="file-uploader"
              onFileSelect={setNewImage}
              reset={resetUploader}
            />
          </div>


        <div className="form-grp update-grp">
          <div className="form-input">
            <p>Name</p>
            <input
              type="text"
              placeholder="Transporter's Name"
              value={initialValues.name}
              onChange={(e) => handleRadioChange(e, "name")}
            />
          </div>
          <div className="form-input">
            <p>Price</p>
            <input
              type="text"
              placeholder="Transporter's Price"
              value={initialValues.price}
              onChange={(e) => handleRadioChange(e, "price")}
            />
          </div>
          
        </div>
        <div className="transp-paper-actions">
          <div className="transp-paper-btn" onClick={handleSave}>
            <p>Save</p>
          </div>
          <div className="transp-paper-btn" onClick={close}>
            <p>Cancel</p>
          </div>
        </div>
      </motion.div>
    </motion.div>
  ) : (
    <div></div>
  );
}

export default TranspPapers;
